/*------------------------------------*  #Hamburger-menu
\*------------------------------------*/
/**
  * Container for the whole 'burger'
  */
.burger {
  cursor: pointer;
  width: 60px;
  height: 45px;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out; }
  @media (min-width: 30em) {
    .burger {
      display: none; } }

/**
  * When the burger lives in a <nav>, position it to the top right
  */
nav .burger {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 40px;
  z-index: 10; }

/**
   * Styling for each of the burger 'buns'
   */
.burger__bun {
  display: block;
  position: absolute;
  height: 9px;
  width: 100%;
  background: #2b2b2b;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out; }
  .header--front .burger__bun {
    background: #fcf7f8; }
  .burger__bun:nth-child(1) {
    top: 0px;
    transform-origin: left center; }
  .burger__bun:nth-child(2) {
    top: 18px;
    transform-origin: left center; }
  .burger__bun:nth-child(3) {
    top: 36px;
    transform-origin: left center; }

/**
  * Turns the burger bun to an X when burger is open
  */
.burger__bun--open:nth-child(1) {
  transform: rotate(45deg);
  top: -3px;
  left: 8px; }

.burger__bun--open:nth-child(2) {
  width: 0%;
  opacity: 0; }

.burger__bun--open:nth-child(3) {
  transform: rotate(-45deg);
  top: 39px;
  left: 8px; }

/**
   * Decorates the buns white when opened
   */
.burger__bun--white {
  background: #fcf7f8; }

